<template>
    <div class=" p-4 rounded"
        :class="[closed ? 'text-black bg-gray-200 hover:border hover:shadow-xl hover:cursor-pointer hover:bg-gray-400' : 'bg-gray-50 text-neutral-500 ']"
        @click="select">
        <div class="flex justify-center">
            <Icon icon="mdi:briefcase" class="text-3xl" />
        </div>
        <p class="text-lg font-bold text-center">{{ box.id }}</p>
    </div>
</template>

<script setup lang="ts">
import { Icon } from '@iconify/vue'
import { defineProps, defineEmits } from 'vue'

const closed = ref(true)

const { box } = defineProps({
    box: {
        type: Object,
        required: true
    }
})

const emit = defineEmits(['select'])

function select() {
    // Emit the select event to the parent component
    if (closed.value) {
        closed.value = false
        emit('select', box.id)
    }
}
</script>

<style scoped>
div {
    width: 100px;
    height: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
</style>
